"use client"

import { useEffect, useRef, useState } from "react"
import { useRouter } from "next/navigation"
import { gsap } from "gsap"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Video, VideoOff, Mic, MicOff, Settings } from "lucide-react"

export default function LobbyPage() {
  const router = useRouter()
  const containerRef = useRef<HTMLDivElement>(null)
  const cardRef = useRef<HTMLDivElement>(null)
  const videoRef = useRef<HTMLVideoElement>(null)
  const titleRef = useRef<HTMLHeadingElement>(null)
  const previewRef = useRef<HTMLDivElement>(null)
  const controlsRef = useRef<HTMLDivElement>(null)
  const optionsRef = useRef<HTMLDivElement>(null)
  const blob1Ref = useRef<HTMLDivElement>(null)
  const blob2Ref = useRef<HTMLDivElement>(null)
  const [meetingCode, setMeetingCode] = useState("")
  const [isVideoEnabled, setIsVideoEnabled] = useState(true)
  const [isAudioEnabled, setIsAudioEnabled] = useState(true)
  const [stream, setStream] = useState<MediaStream | null>(null)

  useEffect(() => {
    const ctx = gsap.context(() => {
      const tl = gsap.timeline({ defaults: { ease: "power3.out" } })
      tl.from(containerRef.current, { opacity: 0, duration: 0.6 })
        .from(
          cardRef.current,
          { y: 40, opacity: 0, duration: 0.9 },
          "<+0.1"
        )
        .from(titleRef.current, { y: 20, opacity: 0, duration: 0.6 }, "<+0.1")
        .from(previewRef.current, { y: 20, opacity: 0, duration: 0.6 }, "<")

      if (controlsRef.current) {
        const buttons = controlsRef.current.querySelectorAll("button")
        tl.from(
          buttons,
          { y: 12, opacity: 0, stagger: 0.08, duration: 0.4 },
          "<+0.05"
        )
      }

      if (optionsRef.current) {
        const optionBlocks = optionsRef.current.querySelectorAll(".option-block")
        tl.from(optionBlocks, { y: 16, opacity: 0, stagger: 0.08, duration: 0.45 }, "<")
      }

      // Animate subtle background blobs
      if (blob1Ref.current) {
        gsap.to(blob1Ref.current, {
          x: 20,
          y: 10,
          scale: 1.05,
          rotate: 8,
          duration: 10,
          ease: "sine.inOut",
          yoyo: true,
          repeat: -1,
        })
      }
      if (blob2Ref.current) {
        gsap.to(blob2Ref.current, {
          x: -24,
          y: -12,
          scale: 1.06,
          rotate: -10,
          duration: 12,
          ease: "sine.inOut",
          yoyo: true,
          repeat: -1,
        })
      }
    })

    return () => ctx.revert()
  }, [])

  useEffect(() => {
    async function setupCamera() {
      try {
        const mediaStream = await navigator.mediaDevices.getUserMedia({
          video: true,
          audio: true,
        })
        setStream(mediaStream)
        if (videoRef.current) {
          videoRef.current.srcObject = mediaStream
        }
      } catch (error) {
        console.error("[v0] Error accessing camera:", error)
      }
    }

    setupCamera()

    return () => {
      if (stream) {
        stream.getTracks().forEach((track) => track.stop())
      }
    }
  }, [])

  useEffect(() => {
    if (stream && videoRef.current) {
      const videoTrack = stream.getVideoTracks()[0]
      if (videoTrack) {
        videoTrack.enabled = isVideoEnabled
      }
    }
  }, [isVideoEnabled, stream])

  useEffect(() => {
    if (stream) {
      const audioTrack = stream.getAudioTracks()[0]
      if (audioTrack) {
        audioTrack.enabled = isAudioEnabled
      }
    }
  }, [isAudioEnabled, stream])

  const handleJoinMeeting = () => {
    if (meetingCode.trim()) {
      router.push(`/meeting/${meetingCode}`)
    }
  }

  const handleNewMeeting = () => {
    const newCode = Math.random().toString(36).substring(2, 10)
    router.push(`/meeting/${newCode}`)
  }

  // Subtle parallax tilt on the card
  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!cardRef.current) return
    const rect = e.currentTarget.getBoundingClientRect()
    const relX = e.clientX - rect.left
    const relY = e.clientY - rect.top
    const midX = rect.width / 2
    const midY = rect.height / 2
    const rotateY = ((relX - midX) / midX) * 6
    const rotateX = -((relY - midY) / midY) * 6
    gsap.to(cardRef.current, {
      rotateY,
      rotateX,
      transformPerspective: 900,
      transformOrigin: "center",
      duration: 0.25,
      ease: "power2.out",
    })
  }

  const handleMouseLeave = () => {
    if (!cardRef.current) return
    gsap.to(cardRef.current, {
      rotateX: 0,
      rotateY: 0,
      duration: 0.5,
      ease: "power3.out",
    })
  }

  return (
    <>
      <div ref={containerRef} className="min-h-screen flex items-center justify-center p-4 relative z-10 overflow-hidden" onMouseMove={handleMouseMove} onMouseLeave={handleMouseLeave}>
        {/* Animated gradient blobs and soft vignette */}
        <div ref={blob1Ref} className="pointer-events-none absolute -z-10 -top-24 -left-24 w-[28rem] h-[28rem] rounded-full bg-gradient-to-tr from-purple-500/25 to-cyan-500/25 blur-[100px]" />
        <div ref={blob2Ref} className="pointer-events-none absolute -z-10 -bottom-32 -right-24 w-[30rem] h-[30rem] rounded-full bg-gradient-to-tr from-rose-500/20 to-amber-500/20 blur-[110px]" />
        <div className="pointer-events-none absolute inset-0 -z-10 bg-[radial-gradient(ellipse_at_center,rgba(255,255,255,0.06),transparent_55%)]" />

        <Card ref={cardRef} className="w-full max-w-5xl p-8 md:p-10 backdrop-blur-xl bg-card/70 border-border/40 shadow-[0_10px_40px_-12px_rgba(0,0,0,0.35)]">
          <div className="grid md:grid-cols-2 gap-8">
            {/* Video Preview */}
            <div className="space-y-5">
              <h2 ref={titleRef} className="text-3xl md:text-4xl font-extrabold text-balance bg-clip-text text-transparent bg-gradient-to-r from-primary to-purple-600 tracking-tight">
                Ready to join?
              </h2>
              <div ref={previewRef} className="relative aspect-video bg-muted rounded-xl overflow-hidden ring-1 ring-border/60">
                {/* Soft glow overlay */}
                <div className="pointer-events-none absolute inset-0 bg-gradient-to-tr from-white/2 to-transparent" />
                {isVideoEnabled ? (
                  <video
                    ref={videoRef}
                    autoPlay
                    playsInline
                    muted
                    className="w-full h-full object-cover scale-x-[-1]"
                  />
                ) : (
                  <div className="w-full h-full flex items-center justify-center bg-muted">
                    <div className="w-20 h-20 rounded-full bg-primary flex items-center justify-center">
                      <span className="text-3xl font-bold text-primary-foreground">
                        {meetingCode ? meetingCode[0].toUpperCase() : "U"}
                      </span>
                    </div>
                  </div>
                )}
              </div>

              {/* Controls */}
              <div ref={controlsRef} className="flex items-center justify-center gap-3">
                <Button
                  size="icon"
                  variant={isVideoEnabled ? "default" : "destructive"}
                  onClick={() => setIsVideoEnabled(!isVideoEnabled)}
                  className="rounded-full h-12 w-12"
                >
                  {isVideoEnabled ? <Video className="h-5 w-5" /> : <VideoOff className="h-5 w-5" />}
                </Button>
                <Button
                  size="icon"
                  variant={isAudioEnabled ? "default" : "destructive"}
                  onClick={() => setIsAudioEnabled(!isAudioEnabled)}
                  className="rounded-full h-12 w-12"
                >
                  {isAudioEnabled ? <Mic className="h-5 w-5" /> : <MicOff className="h-5 w-5" />}
                </Button>
                <Button size="icon" variant="outline" className="rounded-full h-12 w-12 bg-transparent">
                  <Settings className="h-5 w-5" />
                </Button>
              </div>
            </div>

            {/* Meeting Options */}
            <div ref={optionsRef} className="space-y-7 flex flex-col justify-center">
              <div className="space-y-3 option-block">
                <h3 className="text-lg font-semibold bg-clip-text text-transparent bg-gradient-to-r from-purple-600 to-pink-600">
                  Join a meeting
                </h3>
                <div className="flex gap-2">
                  <Input
                    placeholder="Enter meeting code"
                    value={meetingCode}
                    onChange={(e) => setMeetingCode(e.target.value)}
                    onKeyDown={(e) => e.key === "Enter" && handleJoinMeeting()}
                    className="flex-1"
                  />
                  <Button onClick={handleJoinMeeting} disabled={!meetingCode.trim()}>
                    Join
                  </Button>
                </div>
              </div>

              <div className="relative option-block">
                <div className="absolute inset-0 flex items-center">
                  <span className="w-full border-t border-border" />
                </div>
                <div className="relative flex justify-center text-xs uppercase">
                  <span className="bg-card px-2 text-muted-foreground">Or</span>
                </div>
              </div>

              <div className="space-y-3 option-block">
                <h3 className="text-lg font-semibold bg-clip-text text-transparent bg-gradient-to-r from-teal-600 to-cyan-600">
                  Start a new meeting
                </h3>
                <Button onClick={handleNewMeeting} className="w-full" size="lg">
                  Create Meeting
                </Button>
              </div>
            </div>
          </div>
        </Card>
      </div>
    </>
  )
}
